@import '../../styles/base/settings';
@import '../../styles/base/base';

.services-container {
  @extend %services-bg;
  @extend %page-wrapper;
  
  svg {
    display: inline-block;
    width: 45px;
    fill: #fff;
    vertical-align: middle;
    margin: 5px;
  }
  h3 {
    color: white;
    display: inline-block;
    margin: 5px;
    margin-left: 20px;
    vertical-align: middle;
  }
}

.services-content {
  @extend %container-content-middle;
  flex-wrap: wrap;
  text-align: left;

  > div {
    padding: $xl-size $s-size;
    border-bottom: 1px solid rgba(255,255,255,.2);
  }
}
.services-content > div:first-child {
  padding-top: 0;
}

.services-content > div:last-child {
  border-bottom: none;
}

.services-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: $m-size;
}

@media (min-width: 720px) {
  .services-content {
    flex-direction: row;
    justify-content: space-between;
  }
  .services-content > div {
    margin: 0;
    width: calc(45%);
    padding: $xl-size 0;
  }
  .services-content > div:first-child, .services-content > div:nth-child(2) {
    padding-top: 0;
  }
  .services-content > div:last-child {
    border-bottom: 1px solid rgba(255,255,255,.2);
  }
}
