@import '../../styles/base/settings';
@import '../../styles/base/base';

.work-container {
  @extend %page-wrapper;
}
.work-content {
  position: relative;
  @extend %container-content-middle;
  align-items: center;
  justify-content: center;
  max-width: 100% !important;

  h3 {
    margin: 0 0 $s-size;
    color: white;
  }
  p {
    color: white;
  }
  .button-primary {
    margin-bottom: 0;
  }
}


.video {
  display: none;
}

.image {
    width: 1200px;
    max-width: 100%;
    margin: $m-size 0;
}

.work-description {
  width: 475px;
  max-width: 100%;
  padding: 0;
  text-align: left;
  margin: $m-size;
}

.work-list {
  list-style-type: none;
  padding: 10px 0;
  font-size: 1.2rem;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  border-bottom: 1px solid rgba(255,255,255,.2);

  li {
    display: inline-block;
    padding: 7px 10px;
    margin: 5px 10px 5px 0;
    border: 1px solid rgba(255,255,255,.2);
  }
}

.fa-chevron-up, .fa-chevron-down {
  display: none;
}

.stolberg-container, .royal-derby-container, .sf-confectionery-container {
  @extend %work-primary-bg;
}
.button-stolberg, .button-royal-derby, .button-sf-confectionery {
  color: white;
  background: $work-primary;
}

.surfdome-container, .jizz-container {
  @extend %work-secondary-bg;
}
.button-surfdome, .button-jizz{
  color: white;
  background: $work-secondary;
}

@media (min-width: 720px) {
  .work-content {
    flex-direction: column;
  }
  .work-description {
    margin: 0;
    margin-left: $l-size;
  }
  .video {
    display: block;
    box-shadow: -10px 10px 0px rgba(0,0,0,0.15);
    margin: $xl-size 0;
    width: 100%;
    max-height: 100%;
    max-width: 100% !important;
  }
  .image {
    display: none;
  }
  .fa-chevron-up, .fa-chevron-down {
    display: block;
    position: absolute;
    left: 0; right: 0;
    width: 50px;
    margin: auto;
    font-size: 30px;
    color: white;
  }
  .fa-chevron-up {
    top: -20%;
  }
  .fa-chevron-down {
    bottom: -20%;
  }
}

@media (min-width: 920px) {
  .work-content {
    flex-direction: row;
  }
  .work-description {
    margin-left: $xl-size;
  }
}
