@import '../../styles/base/settings';
@import '../../styles/base/base';


header {
  position: relative;
  width: 100%;
  max-width: 100%; 
  z-index: 999;
  width: 1200px;
  margin: 0 auto;
}

.header-content {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  width: 1200px;
  max-width: 100%;
  padding: 5px 10px;
}

.logo {
  padding: 20px 15px;
  color: white;
  font-size: 3rem;
  font-weight: bold;
}

.navigation {
  display: none;
}

.navigation a {
  font: 400 1.6rem/2.5rem 'Roboto', Helvetica, sans-serif;
  color: rgba(255, 255, 255, .6);
  margin: 0 10px;
  // text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 1px;
  padding: 10px;
}

/* Hovers */
.logo:hover {
  opacity: 0.8;
  transition: 0.2s ease-in-out;
}

.navigation a:hover {
  color: white;
  transition: 0.2s ease-in-out;
}

/* Desktop */
@media (min-width: 720px) {
  .header-content {
    position: fixed;
    justify-content: space-between;
    padding: 15px;
    margin: 20px auto 0;
  }

  .logo {
    font-size: 3.5rem;
    padding: 5px;
  }

  .bars {
    display: none;
  }

  .navigation {
    display: block;
  }
}

@media (min-width: 1200px) {
  .header-content {
    padding: 15px 0;
  }
}
