// Colours
$primary: #72accb;
$secondary: #364652;
// Background Colours
$home: darken(#304167, 10%);
$about: darken(#2376E3, 20%);
$services: rgba(234, 87, 88, 1);
$work-primary: darken(#7DBBC3, 20%);
$work-secondary: darken(#78cce8, 20%);
$contact: darken(#26d3c4, 20%);
// Spacing
$s-size: 1.2rem;
$m-size: 1.6rem;
$l-size: 3.2rem;
$xl-size: 4.8rem;
$xxl-size: 10rem;
// Mixins
%page-wrapper {
  position: relative;
  padding: $l-size $s-size;
  color: white;
  text-align: center;
  overflow: hidden;
  min-height: 100vh;
}

%container-content-middle {
  display: flex;
  flex-direction: column;
  width: 1200px;
  max-width: 100%;
  margin: 0 auto;
}

@media (min-width: 720px) {
  %page-wrapper {
    min-height: 100vh;
  }
  %container-content-middle {
    flex-direction: row;
    margin: $xxl-size auto $xl-size;
  }
}

%home-bg {
  background: -moz-linear-gradient(-45deg, rgba(48, 65, 103, 1) 0%, rgba(55, 73, 114, 1) 100%);
  background: -webkit-linear-gradient(-45deg, rgba(48, 65, 103, 1) 0%, rgba(55, 73, 114, 1) 100%);
  background: linear-gradient(to top, #304167 0%, #374972 100%);
}

%about-bg {
  background: -moz-linear-gradient(-45deg, #a3b2be 0%, #b1c0cb 100%);
  background: -webkit-linear-gradient(-45deg, #a3b2be 0%, #b1c0cb 100%);
  background: linear-gradient(to top, #1b60ba 0%, #2376E3 100%);
}

%services-bg {
  background: -moz-linear-gradient(-45deg, rgba(234, 87, 88, 1) 0%, rgba(246, 111, 112, 1) 100%);
  background: -webkit-linear-gradient(-45deg, rgba(234, 87, 88, 1) 0%, rgba(246, 111, 112, 1) 100%);
  background: linear-gradient(to top, #ea5758 0%, #f66f70 100%);
}

%work-primary-bg {
  background: -moz-linear-gradient(-45deg, #57939b 0%, #7DBBC3 100%);
  background: -webkit-linear-gradient(-45deg, #57939b 0%, #7DBBC3 100%);
  background: linear-gradient(to top, #57939b 0%, #7DBBC3 100%);
}

%work-secondary-bg {
  background: -moz-linear-gradient(-45deg, #3B6F91 0%, #427CA2 100%);
  background: -webkit-linear-gradient(-45deg, #3B6F91 0%, #427CA2 100%);
  background: linear-gradient(to top, #68b6d1 0%, #78cce8 100%);
}

%contact-bg {
  background: -moz-linear-gradient(-45deg, rgba(106, 188, 174, 1) 0%, rgba(126, 203, 190, 1) 100%);
  background: -webkit-linear-gradient(-45deg, rgba(106, 188, 174, 1) 0%, rgba(126, 203, 190, 1) 100%);
  background: linear-gradient(to top, #1abaac 0%, #26d3c4 100%);
}