@import '../../styles/base/settings';
@import '../../styles/base/base';

.landing-container {
  @extend %home-bg;
  @extend %page-wrapper;
  color: white;
  min-height: 100vh;
  max-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.landing-content {
  width: 700px;
  max-width: 100%;
  z-index: 10;
  margin-top: -100px;
  margin: 0 auto;

  h1 {
    margin-bottom: $xl-size;
    color: white;
  }
  p {
    font: 400 2rem/3rem 'Roboto', helvetica, sans-serif;
    color: #6d7a97;
    width: 500px;
    max-width: 100%;
    margin: 0 auto
  }
}
.ti-cursor {
  opacity: 0.3;
  margin-left: -15px;
  border-radius: 3px;
}
.landing-content a {
  text-decoration: none;
}

.london {
  position: absolute;
  left: -890px; right: 0; bottom: 0px;
  width: 450%;
  opacity: .1;
  z-index: 0;
}

.layer-1{
  fill: white;
}

.button-landing {
  background: $home;
  margin-top: $xl-size;
}

@media (min-width: 720px) {
  .landing-container {
    max-height: 100vh;
  }
  .london {
    position: absolute;
    left: 0; right: 0;
    width: 110%;
  }

}