@import '../../styles/base/settings';
@import '../../styles/base/base';

.bm-cross-button {
  position: absolute;
  top: 15px !important;
  right: 15px !important;
  width: 30px !important;
  height: 30px !important;
  max-width: 40px;
}

.burger {
  position: fixed;
  top: 15px;
  right: 15px;
  background: #f4f4f4;
  border-radius: 50px;
  height: 45px;
  width: 45px;
  padding: 13px 0;
  box-shadow: 0px 2px 2px rgba(0,0,0,.5);
  cursor: pointer;
}
.burger > div {
  width: 17px;
  height: 2px;
  margin: 3px auto;
  background: $secondary;
}


#sidebar {
  background: $home;
  padding: 60px 20px;
}

.bm-item {
  padding: 0px 0 10px 10px;
  margin: 20px 0;
  color: white;
  border-bottom: 1px solid rgba(255,255,255,.1);
}
.bm-overlay {
  margin-left: -10px;
}

@media (min-width: 720px) {
  .burger {
    display: none;
  }
}
