@import '../../styles/base/settings';
@import '../../styles/base/base';

.contact-container {
  @extend %contact-bg;
  @extend %page-wrapper;
}

.contact-content {
  @extend %container-content-middle;
  flex-direction: column;
  width: 600px;
  max-width: 100%;
  text-align: center;

  p {
    font: 400 2rem/3rem 'Roboto', helvetica, sans-serif;
    width: 500px;
    max-width: 100%;
    margin: 0 auto 30px;
    text-align: center;
  }
}
input, textarea {
  display: block;
  width: 600px;
  max-width: 100%;
  background: rgba(255,255,255,.3);
  border-radius: 3px;
  padding: $m-size;
  color: white;
  letter-spacing: 0.5px;
  border: none;
  margin: 10px auto !important;
  font: 400 1.6rem/1.6rem 'Roboto', Helvetica, sans-serif;
}

.button-contact {
  color: white;
  background: $contact;
}



input:focus, textarea:focus {
  background: rgba(255,255,255,.6);
  outline: none;
}

input::placeholder, textarea::placeholder {
  color: white;
  font-weight: 400;
  letter-spacing: 0.5px;
}

textarea {
  min-height: 200px;
}



