@import '../../styles/base/settings';
@import '../../styles/base/base';

.about-container {
  @extend %about-bg;
  @extend %page-wrapper;
}

.about-content {
  @extend %container-content-middle;

 > div {
    text-align: left;
  }
  p {
    text-align: left;
    margin-bottom: 20px;
    color: white;
  }
}

.profile {
  width: 600px;
  max-width: 100%;
  margin: 5px auto $m-size;
  border-radius: 3px;
  background: rgba(255,255,255,.2);
}
.quote {
  font: 500 2.2rem/3.2rem 'Roboto', Helvetica, sans-serif;
  letter-spacing: 1px;
  font-style: italic;
}

.github-linkedin svg {
  width: 30px;
  margin: 0 20px;
  fill: #fff;
  cursor: pointer
}

.github-linkedin {
  margin: 20px auto;
  text-align: center;
}

.button-cv {
  background: $about;
}

@media (min-width: 720px) {
  .about-content > div {
    padding: 20px;
    width: 50%;
  }
  .github-linkedin svg {
    margin: 0;
    margin-right: 30px;
  }
  .github-linkedin {
    margin: 0 auto;
    text-align: left;
  }

}

