// General

* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  background: white;
  font: 100 1.6rem/2.5rem 'Roboto', Helvetica, sans-serif;
}

a {
  text-decoration: none;
}
.hide {
  display: none !important;
}

.is-active {
  color: white;
}

// Layout
.page-container {
  position: relative;
  max-width: 100%;
}

.page-header {
  height: 170px;
  display: flex;
  justify-content: center;
  align-items: center;

  h2 {
    margin: 0;
  }
}


// Page Transition
.fade-enter {
  opacity: 0.5;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0.5;
  transition: opacity 300ms ease-in;
}


.switch-wrapper {
  position: relative;
}

.switch-wrapper > div {
  position: absolute;
}

// Text
h1 {
  font: 100 6rem/6rem 'Roboto', Helvetica, sans-serif;
  padding: 0;
  margin: $m-size 0;
  color: white;
}

h2 {
  font: 300 3.4rem 'Roboto', Helvetica, sans-serif;
  color: white;
  padding: 0;
  margin: $xl-size 0 $xl-size;
}

h3 {
  font: 400 2.5rem 'Roboto', Helvetica, sans-serif;
  color: white;
  padding: 0;
  margin: $m-size 0;
  letter-spacing: 0.5px;
}
h4 {
  font: 1.7rem 'Roboto', Helvetica, sans-serif;
  color: $secondary;
  padding: 0;
  margin: $m-size 0 0;
}

p {
  font: 100 1.4rem/2.6rem 'Roboto', Helvetica, sans-serif;
  padding: 0;
  margin: 0;
  max-width: 800px;
  color: white;
  letter-spacing: 0.5px;
}


// Tablet
@media (min-width: 480px) {
  h1 {
    font-size: 10rem;
  }

}

// Desktop
@media (min-width: 720px) {
  h1 {
    font-size: 10rem;
  }
  h2 {
    font-size: 5rem;
  }
  h3 {
    font-size: 3rem;
  }
  p {
    font-size: 1.6rem;
    line-height: 3rem;
  }
  .page-header {
    height: 200px;
    align-items: flex-end;
  }
  .is-active {
    color: white !important;
  }

}

